/*
 * Event page styles
 */

/* stylelint-disable selector-max-specificity */
/* stylelint-disable selector-pseudo-element-colon-notation */
/* stylelint-disable declaration-no-important */

body.single-a16z_event,
body.page-template-page_template-event-ty-page {
    padding: 0;
    margin: 0;
    background: $white;

    > .site {
        max-width: none;
        background: transparent;
    }

    .header-new,
    .footer-new {
        .container {
            max-width: rem(1350);
        }
    }

    .site-content {
        margin-bottom: 0;
        padding: 0;
    }

    .content-new {
        padding: 0;
        margin: 0;
        max-width: none;

        > .container {
            max-width: none;
            padding: 0;
        }
    }

    .container {
        max-width: rem(1088);
        margin: 0 auto;
        padding: rem(0 16);

        @include breakpoints(s-medium) {
            padding-left: rem(20);
            padding-right: rem(20);
        }
    }
}

.hero-foreground-image {
    position: absolute;
    bottom: 0;
    z-index: 2;
    left: auto;
    right: 0;

    @include breakpoints(s-medium) {
        left: 55%;
        right: auto;
    }

    img {
        display: block;

        &.img-mobile {
            @include breakpoints(s-medium) {
                display: none;
            }
        }

        &.img-desktop {
            display: none;

            @include breakpoints(s-medium) {
                display: block;
            }
        }
    }
}

.event-content {
    position: relative;
    color: $steel;
    font-size: rem(14);
    line-height: 1.2142;

    @include breakpoints(s-medium) {
        font-size: rem(18);
        line-height: 1.278;
        min-height: calc(100vh - 155px);
    }

    &.visual-curve-tl {
        &:before {
            content: "";
            background: url('/wp-content/themes/a16z-2015/client/images/event/decor-1.png') no-repeat 0 0 / 310px 212px;
            width: 310px;
            height: 212px;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            pointer-events: none;

            @include breakpoints(s-medium) {
                background-size: 380px 250px;
                width: 380px;
                height: 250px;
            }

            @include breakpoints(s-large) {
                background-size: 597px 408px;
                width: 597px;
                height: 408px;
            }
        }
    }

    &.visual-curve-br {
        &:after {
            content: "";
            position: absolute;
            background: url('/wp-content/themes/a16z-2015/client/images/event/decor-2.png') no-repeat 0 0 / 310px 212px;
            background-size: 328px 249px;
            width: 328px;
            height: 249px;
            top: auto;
            left: auto;
            bottom: 0;
            right: 0;
            z-index: 0;
            pointer-events: none;

            @include breakpoints(s-medium) {
                background-size: 450px 350px;
                width: 450px;
                height: 350px;
            }

            @include breakpoints(s-large) {
                background-size: 629px 477px;
                width: 629px;
                height: 477px;
            }
        }
    }

    > .container {
        position: relative;
        z-index: 2;
    }

    .hero {
        position: relative;
        padding: rem(120 0 32);

        @include breakpoints(s-medium) {
            padding: rem(155 0 100);
        }

        &.hero--event-hero-pattern-1 {
            background-image: url(/wp-content/themes/a16z-2015/client/images/event/event-hero-pattern-1.svg);
            background-repeat: no-repeat;
            background-size: cover;
            background-position: 50% 50%;
        }

        &.hero--event-hero-pattern-2 {
            &:before,
            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration01.svg') no-repeat 0 0 / 456px 466px;
                width: 456px;
                height: 466px;
                z-index: 1;
                opacity: 0.35;

                @include breakpoints(none, s-1350) {
                    background-size: 320px 326px;
                    width: 320px;
                    height: 326px;
                }

                @include breakpoints(none, s-large) {
                    background-size: 224px 228px;
                    width: 224px;
                    height: 228px;
                }

                @include breakpoints(none, s-medium) {
                    background-size: 156px 160px;
                    width: 156px;
                    height: 160px;
                }
            }

            &:after {
                right: 0;
                left: auto;
                background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration02.svg') no-repeat 0 0 / 490px 484px;
                width: 490px;
                height: 484px;

                @include breakpoints(none, s-1350) {
                    background-size: 343px 338px;
                    width: 343px;
                    height: 338px;
                }

                @include breakpoints(none, s-large) {
                    background-size: 240px 160px;
                    width: 240px;
                    height: 160px;
                }

                @include breakpoints(none, s-medium) {
                    background-size: 168px 112px;
                    width: 168px;
                    height: 112px;
                }
            }
        }

        &.hero--event-hero-pattern-3 {
            &:before,
            &:after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration05.svg') no-repeat 0 0 / 400px 930px;
                width: 400px;
                height: 930px;
                z-index: -1;
                // opacity: 0.35;
                pointer-events: none;

                @include breakpoints(none, s-1350) {
                    background-size: 300px 697px;
                    width: 300px;
                    height: 697px;
                }

                @include breakpoints(none, s-large) {
                    background-size: 200px 465px;
                    width: 200px;
                    height: 465px;
                }

                @include breakpoints(none, s-medium) {
                    background-size: 140px 325px;
                    width: 140px;
                    height: 325px;
                }
            }

            &:after {
                right: 0;
                left: auto;
                background: url('/wp-content/themes/a16z-2015/client/images/summit-event/se-illustration06.svg') no-repeat 0 0 / 370px 1346px;
                width: 370px;
                height: 1346px;

                @include breakpoints(none, s-1350) {
                    background-size: 277px 1009px;
                    width: 277px;
                    height: 1009px;
                }

                @include breakpoints(none, s-large) {
                    background-size: 185px 673px;
                    width: 185px;
                    height: 673px;
                }

                @include breakpoints(none, s-medium) {
                    background-size: 129px 471px;
                    width: 129px;
                    height: 471px;
                }
            }
        }

        &.hero-simple {
            padding-bottom: rem(42);

            h1 {
                font-size: rem(40);
                line-height: 1.2;
                margin-bottom: rem(36);
                letter-spacing: -0.005em;

                @include breakpoints(none, s-large) {
                    font-size: rem(36);
                    margin-bottom: rem(32);
                }
            }

            p {
                margin-bottom: rem(24);
                font-size: rem(16);
            }

            .event-info {
                font-size: rem(26);
                line-height: 1.23;
                font-weight: 600;
                margin-bottom: rem(24);

                @include breakpoints(none, s-medium) {
                    font-size: rem(22);
                }

                span,
                time {
                    display: block;
                }
            }

            .subtitle {
                margin: rem(0 0 16 0);
            }

            img {
                margin-bottom: rem(10);
            }
        }

        &.hero-summit {
            padding: rem(148 0 96);
            text-align: center;
            color: $white;
            position: relative;
            overflow: hidden;

            > .container {
                max-width: 648px;
                position: relative;
                z-index: 2;
            }

            h1 {
                font-size: rem(85);
                line-height: 1;
                color: $white;
                font-weight: 400;
                letter-spacing: 0;
                margin-bottom: rem(8);

                @include breakpoints(none, s-large) {
                    font-size: rem(65);
                }

                @include breakpoints(none, s-medium) {
                    font-size: rem(45);
                }

                span {
                    font-weight: 700;
                }
            }

            p {
                margin-bottom: rem(24);
                font-size: rem(16);
            }

            .event-info {
                font-size: rem(26);
                line-height: 1.23;
                font-weight: 600;
                margin-bottom: rem(24);

                @include breakpoints(none, s-medium) {
                    font-size: rem(22);
                }

                span,
                time {
                    display: block;
                }
            }
        }

        &.hero-form {
            padding: 126px 0 72px;
            position: relative;
            z-index: 1;
            background: $white;
            overflow: hidden;

            .container {
                max-width: rem(960);
            }

            h1 {
                font-size: rem(30);
                margin-bottom: 20px;
                text-align: center;
                color: $marina;

                @include breakpoints(s-medium) {
                    font-size: rem(40);
                    margin-bottom: 32px;
                }

                + p {
                    text-align: center;
                    margin-bottom: 20px;

                    @include breakpoints(s-medium) {
                        margin-bottom: 32px;
                    }
                }
            }
        }

        .subtitle {
            margin: rem(0 0 15 0);
        }

        h1 {
            font-weight: 700;
            font-size: rem(32);
            line-height: 1.2;
            letter-spacing: -0.01em;
            margin: rem(0 0 16 0);

            @include breakpoints(s-medium) {
                font-size: rem(60);
                margin: rem(0 0 5 0);
                line-height: rem(57);
            }
        }

        .meta {
            font-weight: 300;
            font-size: rem(28);
            line-height: 1.58;

            @include breakpoints(s-medium) {
                font-size: rem(36);
            }
        }
    }

    .hero--inner {
        padding: rem(32 35 32 32);
        background: $white;
        border-radius: rem(20 0 20 20);
        position: relative;
        z-index: 1;

        @include breakpoints(s-medium) {
            padding: rem(44 100 44 65);
            border-radius: rem(70 0 70 70);
        }

        &.hero--inner--narrow {
            h1,
            .subtitle,
            .meta {
                max-width: 520px;
            }
        }
    }

    h2 {
        font-size: rem(22);
        line-height: 1.218;
        font-weight: 500;
        margin-bottom: rem(11);

        @include breakpoints(s-medium) {
            font-size: rem(30);
            margin-bottom: rem(13);
        }
    }

    .intro {
        margin-bottom: rem(24);

        p {
            margin-bottom: rem(10);
        }
    }
}

.registration-form {

    .mktoForm {
        padding: 17px 23px 27px;
        border-radius: 30px 0 30px 30px;
        border: 1px solid #97999B;
        background: white;
        flex-wrap: wrap;

        h3 {
            font: 700 rem(18)/1.2 font-family(sans-serif);
            color: $steel;
            margin-bottom: 4px;
            padding-top: 10px;

            @include breakpoints(s-medium) {
                padding-top: 15px;
                font-size: rem(24);
            }
        }

        .mktoError {
            margin-bottom: 33px;
        }

        .mktoEmailField {

            + .mktoError {
                margin-bottom: 0;
            }
        }

        .mktoFormRow {
            width: 100%;

            &.mktoField--input,
            &.mktoField--select {
              .mktoLabel {
                position: absolute;
                left: 9px;
                top: 14px;
              }
            }

            &.mktoField--select {
                .mktoLabel {
                    background: $white;

                    &:before {
                        content: 'Select ';
                    }
                }

                select.mktoField {
                    -webkit-appearance: none;
                    background: url('/wp-content/themes/a16z-2015/client/images/icon-chevron-down.svg') no-repeat calc(100% - 10px) 50% / 10px auto;
                }
            }

            &.mktoField--checkbox {
              .mktoFieldWrap {
                > .mktoLabel {
                  width: 100% !important;
                  margin-bottom: 10px;
                }
              }
            }

            .mktoLabel {
                font-size: rem(16);
                line-height: 1;
                font-weight: 400 !important;
                font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;
                color: #a0a3a9;
                padding: 0 5px;
                background: $white;
                transition: top 0.3s linear;
                pointer-events: none;
                width: auto !important;
            }

            .mktoHtmlText {
                font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;

                * {
                    padding: 0 !important;
                }
            }

            .mktoFieldWrap {

                &.input-active {

                    .mktoLabel {
                        top: -7px;
                        font-size: 14px;
                    }
                }

                .mktoEmailField {
                    border: 1px solid #d0d0ce;
                }
            }
        }

        .mktoFormCol {
            min-height: 1px;
            width: 100% !important;
            margin-bottom: 17px !important;

            &:first-child:nth-last-child(3) ~ .mktoFormCol {
                padding: 0 !important;
                width: 100% !important;
                margin-bottom: 17px !important;
            }
        }

        input[type="text"],
        input[type="url"],
        input[type="email"],
        input[type="tel"],
        input[type="number"],
        input[type="date"],
        textarea.mktoField,
        select.mktoField {
            border: 1px solid #d0d0ce;
            height: 44px !important;
            outline: none;
            box-shadow: none;
            width: 100%;
            font-size: 16px !important;
            line-height: 1.18 !important;
            color: $steel;
            padding: 12px !important;
            font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;
        }

        .mktoButtonRow {
            width: 100%;
            height: 50px;

            @include breakpoints(s-medium) {
                height: 52px;
            }

            .mktoButtonWrap {
                display: block;
                width: 100%;
            }
        }

        .mktoField--checkbox {
            padding: rem(8);
            background: #f6f7f7;
            margin-bottom: rem(16);

            .mktoFieldWrap {
                > label {
                    background: transparent;
                    font-size: rem(20) !important;
                    top: auto;
                }
            }

            > .mktoFormCol {
                margin-bottom: 0 !important;
            }
        }

        .mktoLogicalField {
            width: 100% !important;

            input[type="checkbox"] {
                opacity: 0;

                &:checked + label {
                    border-color: $marina !important;

                    &:before {
                        background: $marina;
                    }

                    &:after {
                        opacity: 1;
                    }
                }
            }

            label {
                position: relative;
                margin-left: 0;
                line-height: rem(20);
                padding-left: rem(28);
                cursor: pointer;

                &:before {
                    position: absolute;
                    width: rem(20);
                    height: rem(20);
                    background: #e5e6e5;
                    border-radius: 1px;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    transition: background 0.3s linear;
                    content: "";
                }

                &:after {
                    color: #fff;
                    position: absolute;
                    top: 49%;
                    left: rem(5);
                    content: "L";
                    font-size: rem(16);
                    opacity: 0;
                    transition: opacity 0.3s linear;
                    font-family: "arial";
                    transform: scaleX(-1) translateY(-50%) rotate(-35deg);
                }
            }

            .custom-label {
                font-family: font-family(sans-serif);

                &::before,
                &::after {
                    top: 0;
                }

                &::before {
                    transform: none;
                }

                &::after {
                    transform: scaleX(-1) rotate(-35deg);
                }
            }
        }

        .mktoButtonWrap.mktoSimple .mktoButton {
            width: 100%;
            background: $poppy !important;
            color: $poppy !important;
            border: none !important;
            text-align: center;
            font-size: 16px;
            line-height: 1.136;
            font-weight: 700;
            outline: none;
            padding: 12px;
            height: 42px;
            position: relative;
            font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;

            &:after {
                content: "Register";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 16px;
                line-height: 1.136;
                font-weight: 700;
                color: $white;
                font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;
            }

            &:disabled {
                display: block !important;

                &:after {
                    content: none !important;
                }
            }

            img {
                display: none;
            }
        }

        .mktoField {
            float: none;
        }

        &.skip-adding-select-label {
            .mktoFormRow {
                &.mktoField--select {
                    .mktoLabel {
                        &:before {
                            content: none;
                        }
                    }
                }
            }
        }

        .custom-input {
            position: absolute;
        }
    }

    [data-formID="2347"],
    [data-formID="2369"] {
        flex-wrap: wrap;
        justify-content: space-between;

        .mktoFormRow:nth-child(3) + .mktoField--input {
            display: flex !important;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .mktoFormRow,
        .mktoFormRow:nth-child(3).mktoField--select,
        .mktoFormRow:nth-child(3) + .mktoField--input,
        .mktoFormRow:nth-child(10),
        .mktoFormRow:nth-child(11),
        .mktoFormRow:nth-child(14) {
            width: 100% !important;
        }

        .mktoField--input,
        .mktoField--select,
        .mktoFormRow:nth-child(3) + .mktoFormRow .mktoFormCol {
            @include breakpoints(s-medium) {
                width: calc(50% - 10px) !important;
            }
        }

        .mktoFormRow:nth-child(9) .mktoHtmlText,
        .mktoFormRow:nth-child(16) .mktoHtmlText {
            color: $poppy;
        }

        .mktoFormRow:nth-child(9) .mktoFormCol,
        .mktoFormRow:nth-child(13) .mktoFormCol,
        .mktoFormRow:nth-child(16) .mktoFormCol {
            margin-bottom: 10px !important;
        }

        .mktoFormRow:nth-child(13) .mktoHtmlText {
            color: $steel;
        }

        .mktoFormRow:nth-child(3) + .mktoFormRow .mktoClear {
            display: none;
        }
    }

    [data-formID="2372"] {
        &,
        .mktoFormRow:nth-child(4) {
            justify-content: space-between;
            flex-wrap: wrap;
        }

        .mktoClear { display: none !important; }

        .mktoFormRow:nth-child(4) {
            display: flex !important;
        }

        .mktoFormRow:nth-child(4) .mktoFormCol,
        .mktoFormRow:nth-child(4) .mktoFormCol:first-child ~ .mktoFormCol,
        .mktoFormRow:nth-child(4) ~ .mktoField--input {
            @include breakpoints(s-medium) {
                width: calc(50% - 10px) !important;
            }
        }

        .mktoFormRow:nth-child(10).mktoField--input {
            width: 100% !important;
        }

        .mktoFormRow:nth-child(9) .mktoHtmlText,
        .mktoFormRow:nth-child(16) .mktoHtmlText {
            color: $poppy;
        }

        .mktoFormRow:nth-child(13) .mktoHtmlText {
            color: $steel;
        }

        .mktoFormRow:nth-child(9) .mktoFormCol,
        .mktoFormRow:nth-child(13) .mktoFormCol,
        .mktoFormRow:nth-child(16) .mktoFormCol {
            margin-bottom: 10px !important;
        }

        .update-my-registration ~ .mktoButtonRow .mktoButton::after {
            content: 'Update Registration' !important;
        }

        .mktoButtonRow .mktoButton::after {
            content: 'Cancel Registration' !important;
        }

        .mktoFormRow:nth-child(2).mktoField--select .mktoLabel {
            display: none;
        }
    }

    [data-formID="2735"] {
        flex-wrap: wrap;
        justify-content: space-between;

        .mktoFormRow:nth-child(4).mktoField--select .mktoLabel::before {
            content: '' !important;
        }

        .mktoFormRow:nth-child(4) + .mktoField--input {
            display: flex !important;
            flex-wrap: wrap;
            justify-content: space-between;
        }

        .mktoFormRow,
        .mktoFormRow:nth-child(4).mktoField--select,
        .mktoFormRow:nth-child(4) + .mktoField--input,
        .mktoFormRow:nth-child(11),
        .mktoFormRow:nth-child(12),
        .mktoFormRow:nth-child(15) {
            width: 100% !important;
        }

        .mktoField--input,
        .mktoFormRow:nth-child(4) + .mktoFormRow .mktoFormCol {
            @include breakpoints(s-medium) {
                width: calc(50% - 10px) !important;
            }
        }

        .mktoFormRow:nth-child(10) .mktoHtmlText,
        .mktoFormRow:nth-child(17) .mktoHtmlText {
            color: $poppy;
        }

        .mktoFormRow:nth-child(10) .mktoFormCol,
        .mktoFormRow:nth-child(14) .mktoFormCol,
        .mktoFormRow:nth-child(17) .mktoFormCol {
            margin-bottom: 10px !important;
        }

        .mktoFormRow:nth-child(14) .mktoHtmlText {
            color: $steel;
        }

        .mktoFormRow:nth-child(4) + .mktoFormRow .mktoClear {
            display: none;
        }

        h3 {
            color: #D9278D;
        }

        .mktoHtmlText strong {
            color: #4B5058;
        }

        &.mktoForm .mktoButtonWrap.mktoSimple .mktoButton {
            background-color: #3A1C6F !important;

            &:hover {
                background-color: lighten(#3A1C6F, 10%) !important;
            }
        }

        a {
            color: #f60;

            &:hover {
                color: darken(#f60, 15%);
            }
        }

        .mktoField--select.mktoField--select-1,
        .mktoField--select.mktoField--select-8,
        .mktoField--select.mktoField--select-9,
        .mktoField--select.mktoField--select-10,
        .mktoField--select.mktoField--select-11,
        .mktoField--select.mktoField--select-12 {
            width: 100% !important;
        }
    }
}

#registration-form-thank-you {
    text-align: center;
    font-size: 16px;
    color: $steel;
    height: auto !important;

    h2 {
        font-size: 30px;
        font-weight: 700;
        margin-bottom: 10px;

        @include breakpoints(s-medium) {
            font-size: 40px;
            margin-bottom: 20px;
        }

        img {
            display: inline-block;
            vertical-align: middle;
            margin-right: 5px;
            width: 29px;

            @include breakpoints(s-medium) {
                width: 46px;
                margin-right: 10px;
            }
        }
    }
}

#registration-event-form-wrapper {
    p {
        text-align: center;
        margin-bottom: 20px;

        @include breakpoints(s-medium) {
            margin-bottom: 32px;
        }
    }
}

.signup-form-box {
    padding: rem(20 12);
    border: 1px solid $concrete;
    border-radius: 20px 0 20px 20px;
    margin-bottom: rem(20);
    background: $white;

    @include breakpoints(s-medium) {
        padding: rem(33 28 36);
        border-radius: 30px 0 30px 30px;
        margin-bottom: rem(29);
