    h3 {
        font-size: rem(22);
        text-align: center;
        line-height: 1;
        font-weight: 500;
        margin-bottom: rem(16);

        @include breakpoints(s-medium) {
            font-size: rem(24);
            margin-bottom: rem(25);
            text-align: left;
        }
    }

    .mktoForm {
        flex-wrap: wrap;
        padding: 0;

        .mktoError {
            margin-bottom: 33px;
        }

        .mktoEmailField {

            + .mktoError {
                margin-bottom: 0;
            }
        }

        .mktoFormRow {
            width: 100%;

            &.mktoField--input {
              .mktoLabel {
                position: absolute;
                left: 15px;
                top: 17px;
              }
            }

            &.mktoField--checkbox {
              .mktoFieldWrap {
                > .mktoLabel {
                  width: 100% !important;
                  margin-bottom: 10px;
                }
              }
            }

            .mktoLabel {
                font-size: rem(18);
                line-height: 1;
                font-weight: 400 !important;
                font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;
                color: #a0a3a9;
                padding: 0 5px;
                background: $white;
                transition: top 0.3s linear;
                pointer-events: none;
                width: auto !important;

                @include breakpoints(s-medium) {
                    font-size: rem(20);
                }
            }

            .mktoFieldWrap {

                &.input-active {

                    .mktoLabel {
                        top: -7px;
                        font-size: 14px;
                    }
                }

                .mktoEmailField {
                    border: 1px solid #d0d0ce;
                }
            }
        }

        .mktoFormCol {
            min-height: 1px;
            width: 100% !important;
            margin-bottom: 13px !important;

            &:first-child:nth-last-child(3) ~ .mktoFormCol {
                padding: 0 !important;
                width: 100% !important;
                margin-bottom: 13px !important;
            }
        }

        input[type="text"],
        input[type="url"],
        input[type="email"],
        input[type="tel"],
        input[type="number"],
        input[type="date"],
        textarea.mktoField,
        select.mktoField {
            border: 1px solid #d0d0ce;
            height: 48px !important;
            outline: none;
            box-shadow: none;
            width: 100%;
            font-size: 18px !important;
            line-height: 1.389 !important;
            color: $steel;
            padding: 12px 20px !important;
            font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;

            @include breakpoints(s-medium) {
                height: 52px !important;
                font-size: 20px !important;
                line-height: 1.25 !important;
            }
        }

        .mktoButtonRow {
            width: 100%;
            height: 50px;

            @include breakpoints(s-medium) {
                height: 52px;
            }

            .mktoButtonWrap {
                display: block;
                width: 100%;
            }
        }

        .mktoField--checkbox {
            padding: rem(8);
            background: #f6f7f7;
            margin-bottom: rem(16);

            .mktoFieldWrap {
                > label {
                    background: transparent;
                    font-size: rem(20) !important;
                    top: auto;
                }
            }

            > .mktoFormCol {
                margin-bottom: 0 !important;
            }
        }

        .mktoLogicalField {
            width: 100% !important;

            input[type="checkbox"] {
                opacity: 0;

                &:checked + label {
                    border-color: $marina !important;

                    &:before {
                        background: $marina;
                    }

                    &:after {
                        opacity: 1;
                    }
                }
            }

            label {
                position: relative;
                margin-left: 0;
                line-height: rem(20);
                padding-left: rem(28);
                cursor: pointer;

                &:before {
                    position: absolute;
                    width: rem(20);
                    height: rem(20);
                    background: #e5e6e5;
                    border-radius: 1px;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    transition: background 0.3s linear;
                    content: "";
                }

                &:after {
                    color: #fff;
                    position: absolute;
                    top: 49%;
                    left: rem(5);
                    content: "L";
                    font-size: rem(16);
                    opacity: 0;
                    transition: opacity 0.3s linear;
                    font-family: "arial";
                    transform: scaleX(-1) translateY(-50%) rotate(-35deg);
                }
            }
        }

        .mktoButtonWrap.mktoSimple .mktoButton {
            width: 100%;
            background: $marina !important;
            border: none !important;
            text-align: center;
            font-size: 22px;
            line-height: 1.136;
            font-weight: 700;
            color: $white;
            outline: none;
            padding: 12px;
            height: 52px;
            position: relative;
            font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;

            &:after {
                content: "Sign up";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: 20px;
                line-height: 1.136;
                font-weight: 700;
                color: $white;
                font-family: 'proxima-nova', 'Helvetica', 'Arial', sans-serif;

                @include breakpoints(s-medium) {
                    font-size: 22px;
                }
            }

            &:disabled {
                display: block !important;

                &:after {
                    content: none;
                }
            }

            img {
                display: none;
            }
        }
    }
}

