.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: rgba($steel, 0.9);
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: all 0.25s ease-out;

    .modal-active & {
        &.show {
            opacity: 1;
            visibility: visible;
        }
    }

    .modal-inner {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 60px 20px;

        &::after {
            content: '';
            width: 0;
            height: calc(100vh - 120px);
        }
    }

    .modal-box {
        background: $white;
        max-width: 1084px;
        margin: 0 auto;
        width: 100%;
        padding: 30px;
        position: relative;

        @include breakpoints(none, s-medium) {
            padding: 20px;
        }

        .close {
            cursor: pointer;
            position: absolute;
            top: -40px;
            right: -40px;
            font-size: 20px;
            color: rgba($white,0.5);
            transition: color 0.25s ease-out;

