.nice-select {
    min-width: 206px;
    position: relative;

    a {
        color: $steel;
        text-decoration: none;
        display: block;
    }

    > a {
        border: 1px solid $poppy;
        padding: 7px 52px 7px 12px;
        position: relative;
        background: $white;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        transition: border-color 0.25s ease;

        &::after,
        &::before {
            content: '';
            position: absolute;
            right: 0;
            top: 0;
            bottom: 0;
            width: 42px;
            transition: all 0.25s ease;
        }

        &::after {
            background: url('data:image/svg+xml;base64,') no-repeat center/9px auto;
        }

        &::before {
            background-color: $poppy;
        }

        &.active,
        &:hover {
            border-color: $steel;

            &::before {
                background-color: $steel;
            }
        }

        &.active::after {
            transform: scale(1,-1);
        }
    }

    > ul {
        position: absolute;
        z-index: 99;
        border: 1px solid $steel;
        top: 100%;
        left: 0;
        right: 0;
        transform: scale(0.8);
        opacity: 0;
        visibility: hidden;
        transition: all 0.25s ease;
        background: #f2f2f2;
        margin-top: -1px;
        padding: 4px 0;

        &.active {
            opacity: 1;
            transform: scale(1);
            visibility: visible;
        }

        a {
            padding: 4px 10px;
            transition: all 0.25s ease;

            &:hover {
                background-color: $steel;
                color: #f2f2f2;
            }
        }
    }
}

