        display: block;
        font-size: rem(18);
        font-weight: 700;
    }

    .heading {
        p {
            font-size: rem(16);
        }
    }
}

.print-only {
    display: none;
}

// Event: American dynamism summit styles
.event-content {
    .hero.hero-summit {
        .jquery-background-video-wrapper .bg-image {
            background-position: center;
        }
    }

    .hero.hero-summit--left,
    .hero.hero-summit--right {
        .container {
            max-width: 1217px;
        }

        .event-info {
            padding-top: 26px;
        }
    }

    .hero.hero-summit--left {
        text-align: left;
    }

    .hero.hero-summit--right {
        text-align: right;
    }
}

.theme-american-dynamism {
    .hero.hero-summit .event-info,
    .event-component h2,
    #section-id-2.event-component--info h2 {
        color: #0057be;
        font: 700 31px/1.23 $tiempos;
        letter-spacing: 0.02em;

        @include breakpoints($to: 768px) {
            font-size: 26px;
        }
    }

    .event-component--speakers-v2 {
        .container {
            max-width: 983px;
        }

        .speaker-list {
            .name {
                font-size: 18px;
            }

            .designation {
                font-size: 14px;
            }
        }
    }

    #section-id-4.event-component--info h2 {
        color: #00a4b0;
    }

    .event-component--info {
        p {
            font-size: 20px;
        }
    }
}

.theme-culture-leadership {
    .hero.hero-summit .event-info,
    .event-component h2,
    #section-id-2.event-component--info h2 {
        font: 300 31px/1.23 $tiempos;
        letter-spacing: 0.02em;

        @include breakpoints($to: 768px) {
            font-size: 26px;
        }
    }

    #section-id-2.event-component--speakers .name,
    #section-id-2.event-component--speakers-v2 .name {
        color: #fff;
    }

    .registration-form {
        #hero-title {
            font-family: $tiempos;
        }
    }

    #registration-event-form-wrapper {
        a {
            color: #ff6600;

            &:hover {
                color: darken(#ff6600, 10%);
            }
        }

        h6 {
            font-size: 70%;
            margin-bottom: 32px;
            color: #a0a3a9;
        }
    }
}
